<template>
	<div id="member-agreement">
		<public-header :title="title"></public-header>
		<div class="content">
			<div class="richtext" v-html="agreement"></div>
			<div class="tips" v-if="type == 'tips'">
				您可通过阅读完整的<span class="c-danger" @click="util.jsUrl('/pages/member/agreement', {type: 'agreement'})">《用户服务协议》</span>和<span class="c-danger" @click="util.jsUrl('/pages/member/agreement', {type: 'privacy_policy'})">《隐私政策》</span>来了解详细信息
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				title: '服务协议与隐私政策',
				type: 'agreement',
				agreement: '',
				showPreLoading: true,
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					url: 'wmall/member/profile/agreement',
					vue: that,
					data: {
						type: that.type,
					},
					success: function(result) {
						that.agreement = result.agreement;
						that.title = result.title;
						that.util.setWXTitle(result.title);
					}
				});
			}
		},
		mounted() {
			var query = this.util.parseQuery(this.$route.query);
			this.type = query.type;
			this.onLoad();
		},

		watch: {
			$route(){
				var query = this.util.parseQuery(this.$route.query);
				this.type = query.type;
			},
			type() {
				this.showPreLoading = true;
				this.onLoad();
			},
		},
	}
</script>

<style>
	#member-agreement .richtext{
		padding: 10px;
		line-height: 1.5;
		width: 100%;
	}
	#member-agreement .tips{
		padding: 10px;
		line-height: 1.5;
		font-size: 12px;
		color: #7a7a7a;
		word-break: break-word;
	}
</style>


